.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff8dc;
  padding: 10px 30px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
  font-family: Arial, sans-serif;
}

.logo {
  font-size: 14px;
  font-weight: bold;
  text-align: center;
}

.nav ul {
  display: flex;
  list-style: none;
  gap: 20px;
}

.nav ul li {
  font-size: 14px;
  cursor: pointer;
}

.hamburger {
  display: none;
  font-size: 24px;
  cursor: pointer;
}

/* Mobile */
@media (max-width: 768px) {
  .nav {
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background: #fff8dc;
    display: none;
    flex-direction: column;
    padding: 10px;
  }

  .nav.open {
    display: flex;
  }

  .nav ul {
    flex-direction: column;
    gap: 10px;
  }

  .hamburger {
    display: block;
  }
}
