.hero {
  position: relative;
  width: 100%;
  height: 100vh;
  background: url("/public/bg.jpg") no-repeat center center/cover;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 80px 50px;
  box-sizing: border-box;
  flex-wrap: wrap;
}

.overlay {
  background: rgba(255, 255, 255, 0.6);
  padding: 40px;
  max-width: 50%;
}

.big-text {
  font-size: 80px;
  font-family: 'Times New Roman', serif;
  font-weight: bold;
  line-height: 1;
}

.certificate-box h2 {
  font-size: 24px;
}

.right-image img {
  width: 250px;
  border-radius: 20px;
}

/* Tablet */
@media (max-width: 1024px) {
  .big-text {
    font-size: 60px;
  }

  .overlay {
    max-width: 60%;
  }

  .right-image img {
    width: 200px;
  }
}

/* Mobile */
@media (max-width: 768px) {
  .hero {
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 100px 20px;
  }

  .overlay {
    max-width: 100%;
    padding: 20px;
  }

  .big-text {
    font-size: 40px;
  }

  .right-image {
    position: static;
    margin-top: 20px;
  }

  .right-image img {
    width: 180px;
  }
}
